import React, { useState, useEffect } from "react";
import {
  Box,
  Container,
  Typography,
  Button,
  Grid,
  Paper,
  IconButton,
} from "@mui/material";
import { useNavigate } from "react-router-dom";
import { useItinerary } from "../context/ItineraryContext";
import DeleteIcon from "@mui/icons-material/Delete";
import VisibilityIcon from "@mui/icons-material/Visibility";
import ItineraryModal from "../components/ItineraryModal";
import { useAuth } from "../context/AuthContext";

const Main = () => {
  const navigate = useNavigate();
  const { deleteItinerary } = useItinerary();
  const { token, logout } = useAuth();

  const [itineraries, setItineraries] = useState([]); // Inicializado como array vazio
  const [selectedItinerary, setSelectedItinerary] = useState(null);
  const [modalOpen, setModalOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const searchItineraries = async () => {
    setLoading(true);
    setError(null);

    try {
      const response = await fetch(
        "http://ec2-18-204-194-234.compute-1.amazonaws.com:8000/travel_itinerary/my_itineraries/",
        {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
        }
      );

      if (!response.ok) {
        const errorText = await response.text();
        console.error("Erro no servidor:", errorText);
        throw new Error(`Erro ${response.status}: ${response.statusText}`);
      }

      const data = await response.json();

      // Garante que a resposta  um array
      setItineraries(Array.isArray(data) ? data : []);
    } catch (err) {
      console.error("Erro ao buscar itinerrios:", err);
      setError(err.message || "Erro ao buscar itinerrios. Tente novamente.");
    } finally {
      setLoading(false);
    }
  };

  const fetchItineraryDetails = async (id) => {
    if (!token) {
      alert("Token invlido ou sesso expirada");
      logout();
      navigate("/login");
      return;
    }

    setLoading(true);
    setError(null);

    try {
      const response = await fetch(
        `http://ec2-18-204-194-234.compute-1.amazonaws.com:8000/travel_itinerary/my_itineraries/${id}`,
        {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
        }
      );

      if (!response.ok) {
        const errorText = await response.text();
        console.error("Erro ao buscar detalhes do itinerrio:", errorText);
        throw new Error("Erro ao buscar detalhes do itinerrio.");
      }

      const data = await response.json();
      console.log("Dados recebidos do banco:", data);
      setSelectedItinerary(data); // Atualiza o modal com o itinerrio selecionado
      setModalOpen(true);
    } catch (err) {
      console.error("Erro ao buscar itinerrio:", err);
      setError(err.message || "Erro ao buscar detalhes do itinerrio.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    searchItineraries();
  }, []); // Executa ao montar o componente

  const handleOpenModal = (itinerary) => {
    fetchItineraryDetails(itinerary.id);
  };

  const handleCloseModal = () => {
    setSelectedItinerary(null);
    setModalOpen(false);
  };

  return (
    <Box sx={{ bgcolor: "#f9f9f9", minHeight: "100vh" }}>
      <Box
        sx={{
          textAlign: "center",
          py: 8,
          bgcolor: "cobalto_claro.main",
          color: "text.white",
        }}
      >
        <Typography variant="h3" gutterBottom>
          Meus Roteiros
        </Typography>
      </Box>
      <Container sx={{ my: 4 }}>
        <Button
          variant="contained"
          color="secondary"
          onClick={() => navigate("/create")}
          sx={{ mb: 4, width: "300px", height: "3 0px", fontSize: "1.2rem" }}
        >
          Criar Novo Roteiro
        </Button>

        {loading ? (
          <Typography variant="h6" color="textsecondary">
            Carregando...
          </Typography>
        ) : error ? (
          <Typography variant="h6" color="error">
            {error}
          </Typography>
        ) : itineraries.length === 0 ? (
          <Typography variant="h6" color="textsecondary">
            No h roteiros criados ainda. Comece criando um novo!
          </Typography>
        ) : (
          <Grid container spacing={3}>
            {itineraries.map((item) => (
              <Grid item xs={12} sm={8} md={6} key={item.id}>
                <Paper
                  elevation={4}
                  sx={{
                    p: 4,
                    maxWidth: "600px",
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "left",
                  }}
                >
                  <Typography variant="h4" gutterBottom>
                    {item.title}
                  </Typography>
                  <Typography
                    variant="h6"
                    color="textSecondary"
                    sx={{
                      flexGrow: 1,
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                    }}
                  >
                    {item.description || "Sem descrio"}
                  </Typography>
                  <Box mt={2} display="flex" justifyContent="flex-end">
                    <IconButton
                      color="secondary"
                      onClick={() => handleOpenModal(item)}
                    >
                      <VisibilityIcon />
                    </IconButton>
                    <IconButton
                      color="error"
                      onClick={() => deleteItinerary(item.id)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </Box>
                </Paper>
              </Grid>
            ))}
          </Grid>
        )}
      </Container>

      <ItineraryModal
        open={modalOpen}
        onClose={handleCloseModal}
        itinerary={selectedItinerary}
      />
    </Box>
  );
};

export default Main;